<template>
    <div class="main">
        <headerTab />
        <div class="box">
            <div v-html="detailList.title_html" class="news-title"></div>
            <div class="time-text">{{ detailList.timeText }}</div>
            <div class="content" v-html="detailList.content_html"></div>
        </div>
        <bottomVue class="bottom-vue-style" />
    </div>
</template>
<script>
import headerTab from '@/components/officialWebsite/header'
import bottomVue from '@/components/officialWebsite/bottom';
import { getAppDetail } from '../../api/information'

export default {
    components: { headerTab, bottomVue },
    data() {
        return {
            detailList: ''
        }
    },
    methods: {
        async getDetail(id) {
            const detailData = await getAppDetail({ informationId: id })
            console.log(detailData)
            this.detailList = detailData.res[0]
        },
    },
    created() {
        const queryParams = new URLSearchParams(window.location.search);
        const id = queryParams.get('id');
        this.getDetail(id)
    },
    mounted() { },


}
</script>
<style scoped lang="less">
.bottom-vue-style {
    margin-top: auto;
}

/deep/.news-title {
    text-align: center;
    padding: 50px;
    font-weight: 600;
    font-size: 30px;
}

/deep/.content p {
    padding: 20px 0;
}

/deep/.content .url {
    display: none;
}

.time-text {
    padding-bottom: 10px;
    margin: 0 50px;
    font-size: 12px;
    color: #999;
    border-bottom: 1px solid #eee;
}


.main {
    background: #f7f9fc;
    display: flex;
    min-height: 100vh;
    flex-direction: column;
}

.box {
    background: #fff;
    border-radius: 10px;
    width: 1200px;
    margin: 50px auto;

}

.content {
    width: 1000px;
    min-width: 1100px;
    margin: 50px auto;
    margin-top: 10px;
    padding-bottom: 50px;

}
</style>


